// Selectors likely to appear outside of the editor should be prefixed with .ck-content
// This is what makes the LITS styles from the trehub theme also appear in the admin theme
@import "00-base/global/01-colors/_color-vars.scss";
@import "00-base/_01-variables.scss";
@import "00-base/_mixins.scss";
@import "01-atoms/01-links/link/_link.scss";
@import "01-atoms/02-text/00-headings/_headings.scss";
@import "01-atoms/02-text/text/_text.scss";
@import "01-atoms/03-lists/_lists.scss";
@import "01-atoms/07-tables/_tables.scss";

.ck-editor {
  p {
    @extend .paragraph;
  }

  blockquote {
    @extend .blockquote;
  }

  h1 {
    @extend .h1;
  }

  h2,
  .ck-button.ck-heading_heading2 {
    @extend .h2;
  }

  h3,
  .ck-button.ck-heading_heading3 {
    @extend .h3;
  }

  h4,
  .ck-button.ck-heading_heading4 {
    @extend .h4;
  }

  h5,
  .ck-button.ck-heading_heading5 {
    @extend .h5;
  }

  h6,
  .ck-button.ck-heading_heading6 {
    @extend .h6;
  }

  ol,
  ul {
    li {
      @extend .list-item;
    }
  }

  table {
    @extend .table;
  }

  th {
    @extend .table__heading-cell;
  }

  td {
    @extend .table__cell;
  }

  tbody tr {
    @extend .table__row;
  }

  tfoot th {
    @extend .table__footer-cell;
  }
}

// Fix for https: //www.drupal.org/project/drupal/issues/3332416, per #19
.ck.ck-toolbar.ck-toolbar_grouping>.ck-toolbar__items {
  flex-wrap: wrap !important;
}
